@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navigation-bar {    
    background-color: #254E58;
    border-bottom: 1px solid transparent;    
}

.navigation-bar-logo {    
    margin-left: 10%;    
}

.logo {    
    font-family: 'Pacifico', cursive;
    color: #eefceb;
    font-size: 30px;
    margin: 0;
}

.nav-link.active {
    background-color: transparent;
    border: 1px solid #eefceb;
    border-radius: 5px;
}

.nav-link.active a:hover {
    color: #eefceb;
}

.navtop-link a {
    color: #eefceb;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    padding: 0 5px;
}

.navtop-link a:hover {
    color: #112D32;
    text-decoration: none;
}
.footer-bar {
    background-color: #254E58;
    border-top: 1px solid transparent;  
}

.col-item {
    color: #eefceb;
}

.col-item a {
    text-decoration: none;
    font-size: 18px;
    color: white;
}

.col-item a:hover {
    text-decoration: underline;
    color: white
}
.main-img {    
    max-width: 70%;
    height: auto;
    
}

.home-page {
    /* background-image: linear-gradient(to bottom, #254E58, #88BDBC 70%, #254E58); */
    background-color: #88BDBC;
    font-family: 'Merriweather', serif;
}

.home-page-title {
    color: #112D32;
    font-weight: bold;
}

.nav-pills .nav-item.nav-link.active{
    background-color: #254E58;
    color: #eefceb;    
    border-color: transparent;
}

.nav-pills .nav-item.nav-link.active:hover {
    color: #eefceb;
}

.nav-pills .nav-item.nav-link {
    color: #eefceb;
    font-weight: bold;
    font-size: 18px;
}

.nav-pills .nav-item.nav-link:hover {
    color: #112D32;
}

.tab-details h3 {
    font-weight: bold;
}
.skills-page {
    background-color: #88BDBC;
    font-family: 'Merriweather', serif;
}

.skills-page-title {
    color: #112D32;
    font-weight: bold;
}

.skills-page-card.card {
    border: 2px solid #112D32;
    border-radius: 5px;
    color: #eefceb;
}

.skills-page-card .skills-page-card-header {
    background-color: #254E58;    
}

.skills-page-card-title.h5{
    font-weight: 600;
    font-style: italic;
}

.skills-page-card .skills-page-card-body {
    background-color: rgba(37, 78, 88, 0.9);
}
